@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
html,#root{color:#fff;font-family:Montserrat, sans-serif;background-color:#000}::-webkit-scrollbar-track{background-color:#c0c1c2}::-webkit-scrollbar{width:5px;background-color:#c0c1c2}::-webkit-scrollbar-thumb{background-color:#808080}.App{display:flex;justify-content:center;align-items:center;flex-direction:column}.title{margin:20px 0;font-size:28px}.subtitle{margin:20px 0;font-size:22px;font-weight:400}.wrapper{display:flex;flex-direction:column}

.todoForm{margin-bottom:25px}.todoForm__field{position:relative;margin-bottom:15px}.todoForm__label{font-size:18px;margin-right:5px}.todoForm__input,.todoForm__select{width:180px;height:20px;margin:auto 0;border:1px solid #333;font-weight:700;font-size:12px;color:#fff;background-color:transparent;text-align:center;outline:none;cursor:pointer}.todoForm__input:hover,.todoForm__select:hover{border:1px solid #fff}.todoForm__option{background-color:#000;color:#fff}.todoForm__error{width:-webkit-max-content;width:max-content;display:inline-block;position:absolute;margin-left:10px;font-size:14px;top:3px;color:#f00}.todoForm__btn{margin:20px 0;font-size:14px;width:110px;height:35px;color:#666;border:1px solid #333;background-color:transparent}.todoForm__btn:hover{cursor:pointer;color:#fff;border-color:#fff}

.UserInfo{color:#386ac9;font-style:italic}

.TodoInfo{width:-webkit-max-content;width:max-content;padding:8px 13px;margin-bottom:20px;border-radius:2px;border:1px solid #fff;background-color:transparent}.TodoInfo__title{margin:4px 0;font-size:inherit;color:#f00}.TodoInfo--completed .TodoInfo__title{color:#4dc94d}

